import { classMap } from 'lit/directives/class-map.js';
import { html } from 'lit';
import { property, state } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import componentStyles from '../../styles/component.styles';
import Icon from '../icon/icon.component';
import styles from './radio.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @summary Radios allow the user to select a single option from a group.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net
 * @status stable
 * @since 2.0
 *
 * @dependency koerber-icon
 *
 * @slot - The radio's label.
 *
 * @event koerber-blur - Emitted when the control loses focus.
 * @event koerber-focus - Emitted when the control gains focus.
 * @event koerber-input - Emitted when the control receives user input.
 * @event koerber-change - Emitted when the control's checked state changes.
 *
 * @csspart base - The component's base wrapper.
 * @csspart control - The circular container that wraps the radio's checked state.
 * @csspart control--checked - The radio control when the radio is checked.
 * @csspart checked-icon - The checked icon, an `<koerber-icon>` element.
 * @csspart label - The container that wraps the radio's label.
 */
export default class Radio extends UIKitElement {
  static styles: CSSResultGroup = [componentStyles, styles];
  static dependencies = { 'koerber-icon': Icon };

  @state() checked = false;
  @state() protected hasFocus = false;

  /** The radio's value. When selected, the radio group will receive this value. */
  @property() value: string;

  /** Label of the radio. */
  @property() label: string;

  /**
   * The radio's size. When used inside a radio group, the size will be determined by the radio group's size so this
   * attribute can typically be omitted.
   */
  @property({ reflect: true }) size: 'standard' | 'comfortable' = 'standard';

  /** Disables the radio. */
  @property({ type: Boolean, reflect: true }) disabled = false;

  @property({ type: Boolean, reflect: true }) error = false;

  /** The component accessible label name */
  @property({ attribute: 'accessible-name', reflect: true }) accessibleName: string;

  /** The component accessible description */
  @property({ attribute: 'accessible-description', reflect: true }) accessibleDescription: string;

  connectedCallback() {
    super.connectedCallback();
    this.handleBlur = this.handleBlur.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleFocus = this.handleFocus.bind(this);

    this.setInitialAttributes();
    this.addEventListeners();
  }

  disconnectedCallback() {
    this.removeEventListeners();
  }

  private addEventListeners() {
    this.addEventListener('blur', this.handleBlur);
    this.addEventListener('click', this.handleClick);
    this.addEventListener('focus', this.handleFocus);
  }

  private removeEventListeners() {
    this.removeEventListener('blur', this.handleBlur);
    this.removeEventListener('click', this.handleClick);
    this.removeEventListener('focus', this.handleFocus);
  }

  private handleClick() {
    if (!this.disabled) {
      const parentElementName = this.parentElement?.tagName.toLowerCase();
      if (parentElementName === 'koerber-radio-group') {
        return;
      }

      if (!this.checked) {
        this.emit('koerber-input');
        this.emit('koerber-change');
      }

      this.checked = true;
    }
  }

  private handleBlur() {
    this.hasFocus = false;
    this.emit('koerber-blur');
  }

  private handleFocus() {
    this.hasFocus = true;
    this.emit('koerber-focus');
  }

  private setInitialAttributes() {
    this.setAttribute('role', 'radio');
    this.setAttribute('tabindex', '-1');
    this.setAttribute('aria-disabled', this.disabled ? 'true' : 'false');

    if (this.accessibleName) {
      this.setAttribute('aria-label', this.accessibleName);
    }

    if (this.accessibleDescription) {
      this.setAttribute('aria-description', this.accessibleDescription);
    }
  }

  @watch('checked')
  handleCheckedChange() {
    this.setAttribute('aria-checked', this.checked ? 'true' : 'false');
    this.setAttribute('tabindex', this.checked ? '0' : '-1');
  }

  @watch('disabled', { waitUntilFirstUpdate: true })
  handleDisabledChange() {
    this.setAttribute('aria-disabled', this.disabled ? 'true' : 'false');
  }

  render() {
    return html`
      <span
        part="base"
        class=${classMap({
          radio: true,
          'radio--checked': this.checked,
          'radio--disabled': this.disabled,
          'radio--error': this.error,
          'radio--focused': this.hasFocus,
          'radio--standard': this.size === 'standard',
          'radio--comfortable': this.size === 'comfortable'
        })}
      >
        <span part="${`control${this.checked ? ' control--checked' : ''}`}" class="radio__control">
          ${this.checked
            ? html`
                <koerber-icon
                  part="checked-icon"
                  size="medium"
                  class=${classMap({
                    'radio__checked-icon': true,
                    'icon--medium': true
                  })}
                  library="system"
                  name="circle"
                ></koerber-icon>
              `
            : ''}
        </span>

        <slot part="label" class="radio__label">
          <koerber-form-field-label label="${this.label}"></koerber-form-field-label>
        </slot>
      </span>
    `;
  }
}
